import React from 'react';
import EmployeePage from './employee-page';
import { useParams } from 'react-router';
import { driver } from '../../../mock/employee';

type EmployeeId = {
  id: string;
};

const EmployeePageContainer = () => {
  const { id }: EmployeeId = useParams();
  const driverData = driver.find(item => item.id === Number(id));
  return <EmployeePage driverData={driverData} />;
};

export default EmployeePageContainer;
